import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';
import { User } from '../models/user';
import { environment } from '../../environments/environment';
import { AppConfigService } from './app-config.service';
import { AppService } from './app.service';

@Injectable({
  providedIn: 'root',
})
export class UserService {
  constructor(private http: HttpClient,
    private configService: AppService
  ) {}

  login(username: string, password: string): Observable<any> {
    const params = {
      username,
      password,
    };
    return this.http.post(`${this.configService.config()?.apiUrl}/login`, params);
  }

  signup(user:User): Observable<any> {
    return this.http.post(`${this.configService.config()?.apiUrl}/register`, user);
  }

  getUsers(): Observable<User[]> {
    return this.http.get<User[]>(`localhost:5000/api/v1/users/`);
  }
}
